body {
 
  /* background-image: url('D:\NReact\myportfolio\src\images\i.jpg');
   */
  /* background-size: cover;
  background-position: center;
  background-repeat: no-repeat; */

  background-color: rgba(10, 10, 10, 0.999);
  color: white;
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
}

.container {
  max-width: 100%;
  margin: auto;
  
  opacity: 0;
  animation: fadeIn 1.5s forwards;
  
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.header {
  text-align: center;
  font-size: 2.5rem;
  font-weight: bold;
  margin-bottom: 10px;
  animation: slideDown 1s ease-in-out;
}

@keyframes slideDown {
  from {
    transform: translateY(-20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.contact-info {
  text-align: center;
  color: #f39c12;
  margin-bottom: 20px;
}

.link {
  color: #f39c12;
  text-decoration: none;
}

.section {
  background-color: #0d1517;
  padding: 60px;
  max-width: 1000px;
  border-radius: 8px;
  
  margin: 20px auto 90px auto;  /* Adds 212px bottom space */
  align-content: left;
  text-align: left;  /* Centers text inside */
  transform: translateY(30px);
  opacity: 0;
  animation: slideUp 3s ease-in-out forwards;
  box-shadow: 0px 4px 10px rgba(219, 147, 4, 0.9); /* Adds subtle shadow */
}


@keyframes slideUp {
  from {
    transform: translateY(30px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

h2 {
  color: #f39c12;
  text-align: center;
  margin-bottom: 10px;
}

.card {
  background-color: #1a1816;
  padding: 15px;
  border-radius: 8px;
  margin-bottom: 10px;
  transition: transform 0.3s ease-in-out;
}

.card:hover {
  transform: scale(1.05);
}

.card h3 {
  color: #f39c12;
  margin-bottom: 5px;
}

.skills {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
}

.skill-badge {
  background-color: #f39c12;
  padding: 8px 15px;
  border-radius: 5px;
  animation: popIn 0.5s ease-in-out;
}

@keyframes popIn {
  from {
    transform: scale(0.8);
    opacity: 0;
  }
  to {
    transform: scale(1);
    opacity: 1;
  }
}

.projects {
  display: flex;
  justify-content: center;
}

.project-card {
  background-color: #322f2c;
  padding: 10px 20px;
  border-radius: 5px;
  text-decoration: none;
  color: white;
  font-weight: bold;
  transition: transform 0.3s ease-in-out;
}

.project-card:hover {
  transform: scale(1.1);
}

.certifications {
  list-style-type: none;
  padding: 0;
  text-align: center;
}

.certifications li {
  background-color: #0c1b21;
  padding: 10px;
  margin: 5px 0;
  border-radius: 5px;
  transition: transform 0.3s;
}

.certifications li:hover {
  transform: scale(1.05);
}

.contact {
  text-align: center;
}

.icons {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-top: 10px;
}

.icon {
  font-size: 24px;
  color: #f39c12;
  transition: transform 0.3s ease-in-out;
}

.icon:hover {
  transform: scale(1.2);
}

.download-section {
  text-align: center;
  margin-top: 20px;
}

.download-btn {
  background-color: #f39c12;
  color: white;
  padding: 10px 20px;
  border-radius: 5px;
  text-decoration: none;
  font-weight: bold;
  display: inline-block;
  transition: background-color 0.3s ease-in-out;
}

.download-btn:hover {
  background-color: #f39c12;
}

.about-section {
  text-align: center;
  padding: 30px;
  background-color: #f8f9fa; /* Light gray background */
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Soft shadow */
  max-width: 800px; /* Increased width for better layout */
  margin: auto;
}

.about-content {
  display: flex;
  align-items: center; /* Aligns image and text in the center */
  gap: 30px; /* Adds more space between image and text */
  justify-content: space-between; /* Improves spacing */
  text-align: left; /* Ensures text aligns properly */
}

.profile-image {
  width: 450px;
  height: 550px;
  border-radius: 10%; /* Circular image */
  object-fit: cover;
  justify-content: flex-end;
  
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2); /* Slight shadow for better visibility */
}

.coding-image {
  width: 100px;
  height: 100px;
  border-radius: 10%; /* Circular image */
  object-fit: cover;
  justify-content: flex-end;
 
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2); /* Slight shadow for better visibility */
}

.about-content p {
  flex: 1; /* Makes text take the remaining space */
  font-size: 18px;
  line-height: 1.6; /* Improves readability */
  
}

/* Responsive Design */
@media (max-width: 768px) {
  .about-content {
    flex-direction: column; /* Stack image and text vertically on small screens */
    text-align: center;
  }


  .about-content p {
    max-width: 100%; /* Allow text to expand fully */
    font-size: 16px;
  }
}


.experience-image {
  width: 140px;
  height: 40px;
  border-radius: 10px;
  object-fit: contain;
  border: 2px solid #ddd;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes bounce {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-5px);
  }
}
.skills {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px; /* Increased gap */
  padding: 20px;
}

.skill-badge {
  display: flex;
  align-items: center;
  gap: 40px;
  background: white;
  padding: 1px 2px; /* Increased padding */
  border-radius: 25px;
  box-shadow: 0 4px 15px rgba(241, 167, 6, 0.9); /* More depth */
  font-size: 60px; /* Increased font size */
  font-stretch: expanded;
  font-weight: 800;
  border: 1px solid #ddd;
  opacity: 0;
  animation: fadeIn 0.4s ease-in forwards;
  transition: all 0.3s ease-in-out;
}

.skill-badge:hover {
  background-color: #4CAF50;
  color: white;
  transform: scale(1.15); /* Slightly larger hover effect */
  animation: bounce 0.6s ease-in-out infinite;
}


.download-button {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 12px 20px;
  background-color: #007bff;
  color: white;
  font-size: 16px;
  font-weight: bold;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  transition: all 0.3s ease-in-out;
}

.download-button:hover {
  background-color: #1212f3;
  transform: translateY(-2px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.3);
}

.download-button:active {
  background-color: #f39c12;
  transform: translateY(1px);
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.2);
}

.download-icon {
  font-size: 20px;
}


.timeline-container {
  max-width: 900px;
  margin: auto;
  padding: 40px;
}

.section-title {
  text-align: center;
  font-size: 32px;
  color: #f39c12;
  font-weight: bold;
  margin-bottom: 20px;
}

.timeline {
  position: relative;
  padding: 20px 0;
}

.timeline-item {
  display: flex;
  align-items: flex-start;
  margin-bottom: 30px;
  opacity: 0; /* Hidden initially for animation */
}

.timeline-icon {
  width: 50px;
  height: 50px;
  background-color: #f39c12;
  color: white;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 22px;
  margin-right: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.timeline-content {
  background: #f3f3f3;
  padding: 15px 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 100%;
  transform: translateY(30px);
  transition: all 0.5s ease-out;
}

.timeline-content h3 {
  font-size: 20px;
  color: #333;
  margin-bottom: 10px;
}

.timeline-content ul {
  padding-left: 20px;
}

.timeline-content ul li {
  font-size: 16px;
  margin-bottom: 5px;
  color: #555;
}

.experience-image {
  width: 100px;
  height: 100px;
  object-fit: cover;
  border-radius: 8px;
  margin-bottom: 10px;
  display: block;
}

@media (max-width: 768px) {
  .timeline-item {
    flex-direction: column;
    align-items: center;
  }

  .timeline-icon {
    margin-bottom: 10px;
  }
}

