body {
  /* background-image: url('./images/background.gif'); Update with your image path */
  color: white;
  font-family: Arial, sans-serif;
  text-align: center;
  padding: 20px 20px 20px 20px ;
}

h2 {
  color: #f39c12;
  margin-bottom: 10px;
}


.skills span, .projects a {
  background-color: #3a2f24;
  padding: 10px;
  margin: 5px;
  border-radius: 8px;
  display: inline-block;
}

.projects a:hover {
  background-color: #1f2937;
}

.contact-icons a {
  margin: 10px;
  font-size: 24px;
  color: #f39c12;
}

.navbar {
  width: 100%;
  height: 90px;
  background-color: #1a1919;

  position: sticky;
  top:10px;
  z-index: 1000;

  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 20px 20px 20px ;

  color: #f1f0f4;
  box-shadow: 0px 4px 10px rgba(219, 147, 4, 0.9); /* Adds subtle shadow */
  /* Center horizontally */
  
}


.navbar-container {
  display: flex;
  width: 90%;
  justify-content: space-between;
  align-items: center;
}

.navbar-brand {
  font-size: 20px;
  font-weight: bold;
}

.navbar-links {
  display: flex;
  align-items: center;
  gap: 15px;
}

.nav-link {
  color: white;
  transition: color 0.3s;
}

.nav-link:hover {
  color: #f39c12;
}

.nav-contact {
  font-size: 14px;
}


.hero-section {
  width: 100%; /* Full viewport width */
  height: 80vh; /* Full viewport height */
 
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  display: flex;
  flex-direction: column;
  justify-content: center; /* Center content vertically */
  align-items: center; /* Center content horizontally */
  color: rgb(232, 217, 217); /* Ensure text is readable */

}


@keyframes wave-animation {
  0% { transform: rotate(0deg); }
  10% { transform: rotate(14deg); }
  20% { transform: rotate(-8deg); }
  30% { transform: rotate(14deg); }
  40% { transform: rotate(-4deg); }
  50% { transform: rotate(10deg); }
  60% { transform: rotate(0deg); }
  100% { transform: rotate(0deg); }
}
.profile-image {
  width: 250px;
  height: 250px;
  border-radius: 50%;
  object-fit: cover;
  
  transition: transform 0.3s ease;
}

.profile-image:hover {
  animation: bounce 0.6s ease;
}

/* Keyframes for bounce */
@keyframes bounce {
  0%, 100% {
    transform: translateY(0);
  }
  30% {
    transform: translateY(-15px);
  }
  60% {
    transform: translateY(-8px);
  }
}






